import { mapState, mapGetters } from 'vuex';

export default {
	data() {
		return {
			personinfo: {},
			downOption: {
				auto: false //是否在初始化后,自动执行下拉回调callback; 默认true
			},
			upOption: {
				auto: false,
				textNoMore: '-- 已经到底了 --'
			},
			work_detail: [],
			loading: true,
			wages: ''
		};
	},
	onLoad(option) {
		this.personinfo = JSON.parse(decodeURIComponent(option.item));
		this.wages = this.$_.get(this.personinfo, 'wages', '');
		uni.$on('refreshList', () => {
			console.log('refreshList');
			this.init();
		});
		this.init();
	},
	onUnload() {
		uni.$off('refreshJwt');
	},
	computed: {
		c_CustomBar() {
			return this.CustomBar;
		},
		...mapGetters(['id']),
		no_data() {
			return !this.loading && this.work_detail.length == 0;
		},
		sum_work_tiem() {
			let time = 0;
			this.work_detail.forEach(item => {
				time += this.$_.toNumber(item.workTime);
			});
			return time;
		},
		sum_price() {
			return this.$_.get(this.personinfo, 'cumulativeHours', 0) * this.$_.toNumber(this.wages);
		}
	},
	methods: {
		init() {
			this.get_work_time();
		},
		downCallback(mescroll) {
			// loadSwiper();
			// 下拉刷新的回调,默认重置上拉加载列表为第一页 (自动执行 mescroll.num=1, 再触发upCallback方法 )
			mescroll.resetUpScroll();
		},
		/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
		async upCallback(mescroll) {
			//联网加载数据
			await this.get_work_time();
			mescroll.endSuccess(false);
			// setTimeout(() => {
			// 	mescroll.endErr();
			// }, 1000);
		},
		get_work_time() {
			this.$http
				.get('personwx.personproworktimedetail/1.0/', {
					pid: this.personinfo.id,
					proId:this.personinfo.proId
				})
				.then(res => {
					console.log(res);
					this.work_detail = res.data.data;
				})
				.finally(() => {
					this.loading = false;
				});
		},
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		sub_settle() {
			if(this.wages === ''){
				uni.showToast({
				    title: '请输入工价',
				    duration: 2000,
					icon:"none"
				});
				return
			}
			uni.showModal({
				title: '提示',
				content: `当前结算金额为${this.sum_price}元，确认是否结算`,
				confirmText: '确认结算',
				success: res => {
					if (res.confirm) {
						console.log('用户点击确定');
						this.$http
							.post('personwx.personproworktimeseet/1.0/', {
								pid: this.personinfo.id,
								proId: this.personinfo.proId,
								wages: this.wages
							})
							.then(res => {
								console.log(res);
								uni.showToast({
								    title: '结算成功',
								    duration: 2000,
									icon:'success',
									success: () => {
										setTimeout(() =>{
											this.back();
										},300)
										
									}
								});
							});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		deliveryPeriod(time) {
			return this.$utils.format_date(time);
		},
		
	}
};
